import "./Banner.css";

const imgUrl =
  "data:image/png;base64,";

const Banner = () => {
  return (
    <div className="Banner">
      <div className="Banner-container">
        <img className="Banner-logo" src={imgUrl} alt="logo" />
        <h1 className="Banner-title">La maison jungle</h1>
      </div>
    </div>
  );
};

export default Banner;
